
.secondLayer {
	position: absolute;
	left: 10%;
	top: 10%;
	margin-left: -10px;
	margin-top: -10px;
	border: 10px solid rgba(255, 255, 255, .5);
	width: 80%;
	height: 80%;
}

.titleP {
	text-align: center;
}
.copyRightP {
	position: absolute;
	text-align: left;
	bottom: 0;
	left: 5px;
	color: rgba(255, 255, 255, .5);
}
.teamP {
	position: absolute;
	text-align: right;
	bottom: 0;
	right: 5px;
	color: rgba(255, 255, 255, .5);
}

.searchIcon {
	position: absolute;
	bottom:30%;
	right:10%;
	width: 100%;
	height: auto;
}

.horizontalLine {
	position: absolute;
	bottom: 50%;
	left: 15%;
	height: 3px;
	width: 70%;
	background-color: rgba(255, 255, 255, .5);;
}

.personInfo {
	position: absolute;
	right: 13%;
	top: 25%;
}

.chatIcon {
	position: absolute;
	left: 25%;
	bottom: 30%;
}

.settingIcon {
	position: absolute;
	left: 60%;
	bottom: 30%;
}

.verticalLine {
	position: absolute;
	height: 60%;
	width: 3px;
	top: 20%;
	left: 60%;
	background-color: rgba(255, 255, 255, .5);;
}



