@import url('https://fonts.googleapis.com/css?family=Amiko|Quicksand');
.headerBlock {
  position: absolute;
  width: 100%;
  /*height: auto;*/
  height: 50px; /* star */
  background-color: #f8f8f8;
  text-align: center;
  padding: 5px;
  z-index: 100;
}

.previousPage {
  position: absolute;
  bottom: 50%;
  left: 0;
  -webkit-transform: translateY(50%);
      -ms-transform: translateY(50%);
          transform: translateY(50%);
  margin-left: 10px;
  height: 22px;
  width: 22px;
}

.headerName{
  position: absolute;
  bottom: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(50%);
      -ms-transform: translateX(-50%) translateY(50%);
          transform: translateX(-50%) translateY(50%);
  font-family: 'Amiko', sans-serif;
  color:black;
}


