
* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	position: relative;
}

body, html {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    position: relative;
}

.titleP {
	overflow: hidden;
	text-align: center;
	margin: 0;
}

.backgroundImage { 
    background-image: url("../img/background/background.png");
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.copyRightP {
	position: absolute;
	text-align: left;
	bottom: 0;
	left: 5px;
	color: rgba(255, 255, 255, .5);
}

.teamP {
	position: absolute;
	text-align: right;
	bottom: 0;
	right: 5px;
	color: rgba(255, 255, 255, .5);
}

.hideUI {
	display: none;
}


