@import url('https://fonts.googleapis.com/css?family=Amiko|Quicksand');

#image-form {
  z-index: 4;
  /*width: 0;*/
  right: 8%;
  bottom: 0px;
  position: absolute;
}

.chatGround{
  position: absolute;
  left:6%;
  top: 13%;
  height: 85%;
  width: 88%;
  border-radius: 15px !important;
  z-index: 2;
  background-color: #f2f2f2;
}
.realChatGround::-webkit-scrollbar { 
    display: none; 
}
.realChatGround{
  position: absolute;
  left:6%;
  top:3%;
  height: 83%;
  width: 88%;
  z-index: 2;
  overflow-y: auto;
  overflow-x: hidden;
}

.myMessage{
  font-family: 'Quicksand', sans-serif;
  position: relative;
  float: right;
  right: 3px;
  clear: both;
  margin-top:10px;
  max-width: 60%;
  height: auto;
  border-radius: 7px;
  position: relative;
  z-index: 3;
  background-color: #e6e6e6;
  padding: 2px 6px 2px 6px;
  font-size:11px;
  word-break: break-all;
}
.others_Message{
  font-family: 'Quicksand', sans-serif;
  position: relative;
  float: left;
  left: 3px;
  clear: both;
  margin-top:10px;
  max-width: 60%;
  height: auto;
  border-radius: 7px;
  position: relative;
  z-index: 3;
  background-color: #e6e6e6;
  padding: 2px 6px 2px 6px;
  font-size:11px;
  word-break: break-all;
}

.blockBWMessage{
  height:15px;
}

#input-section {
  position: absolute;
  bottom: 10px !important;
  left: 5%;
  width: 90%;
  /*height: 22px;*/
}

.inputForm{
  /*position: absolute;
  bottom: 10px !important;
  left: 5%;
  width: 90%;*/
  height: 22px;
}

.inputBlock{
  width: 100%; 
  height: 100%;
}

.inputMessage{
  z-index: 4;
  position: absolute;
  left: 5%;
  width: 90%;
  height: 100%;
  border-radius: 30px;
  background-color:#e6e6e6;
  border: 1px solid #e6e6e6;
}
.inputMessage[type="text"]
{
    font-size:11px;
}
.inputMessage:focus {
  outline: none;
}
.LoadPrevButton{
  font-family: 'Quicksand', sans-serif !important;
  top:0;
}
.LoadAfterButton{
  position: relative;
  float: left;
  bottom:0;
  clear: both;
}
#mount-point {
    height: 100%;
}
#mediaCapture {
  z-index: 4;
  /*width: 16%;*/
  /*right: 2%;*/
  /*bottom: 26px ;*/
  /*position: absolute;*/
}

#selectPicIcon{
	height: 22px;
}

#inputHidden {
  display: none;
}

.othersImageMessage {
  position: relative;
  float: left;
  left: 3px;
  clear: both;
  z-index: 3;
  width: 30%;
  height: auto;
  margin-top:10px;
  border-radius: 7px;
}
.myImageMessage {
  position: relative;
  float: right;
  right: 3px;
  clear: both;
  z-index: 3;
  width: 30%;
  height: auto;
  margin-top:10px;
  border-radius: 7px;
}
.zindex5{
  z-index: 5;
}

#lastRender{
  float: right;
  clear: both;
  /*position: absolute;*/
  /*bottom: 0;*/
}



