
html, body {
    height: 100%;
    margin: 0; 
    padding: 0;
}
body > #mount-point {
	height: 100%;
}
.backgroundImage { 
    background-image: url("../img/background/background.png");
    height: 100%; 
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.secondLayer {
	position: absolute;
	left: 10%;
	top: 10%;
	margin-left: -10px;
	margin-top: -10px;
	border: 10px solid rgba(255, 255, 255, .5);
	width: 80%;
	height: 80%;
}
.titleP {
	text-align: center;
}
.copyRightP {
	position: absolute;
	text-align: left;
	bottom: 0;
	left: 5px;
	color: rgba(255, 255, 255, .5);

}
.fbSignIn {
	position: absolute;
	top: 60%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
}
.teamP {
	position: absolute;
	text-align: right;
	bottom: 0;
	right: 5px;
	color: rgba(255, 255, 255, .5);

}
.rudder {
	width: 30%;
	height: auto;
	top: 10%;
	left: 35%;
	position: absolute;
}

#login-form {
	width: 90%;
	background-color: white;
	padding: 5px;
	position: absolute;
	top: 40%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
}

#login-form * {
	display: block;
}

.b-signin {
	margin-top: 25px;
	display: block;
	width: 100%;
}


